import React, { Component } from "react";
import RoseList from "./RoseList";
import RoseDetail from "./RoseDetail";
import { Switch, Route, Redirect } from "react-router-dom";
class Routes extends Component {
  render() {
    const getRose = props => {
      let name = props.match.params.name;
      let currentRose = this.props.roses.find(
        rose => rose.name.toLowerCase() === name.toLowerCase()
      );
      return <RoseDetail {...props} rose={currentRose} />;
    };
    return (
      <Switch>
        <Route
          exact
          path="/roses"
          render={() => <RoseList roses={this.props.roses} />}
        />
        <Route exact path="/roses/:name" render={getRose} />
        <Redirect to="/roses" />
      </Switch>
    );
  }
}

export default Routes;
