import React, { Component } from "react";
import { NavLink, Link } from "react-router-dom";

class Navbar extends Component {
  render() {
    let roseLinks = this.props.roses.map(rose => {
      return (
        <li className="nav-item text-capitalize" key={rose.name}>
          <NavLink exact to={`/roses/${rose.name}`} className="nav-link">
            {rose.name}
          </NavLink>
        </li>
      );
    });

    return (
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <div className="container-fluid">
          <Link className="navbar-brand text-capitalize" to="/roses">
            rose finder
          </Link>

          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon" />
          </button>
          <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav">
              <li className="nav-item">
                <NavLink exact to="/roses" className="nav-link">
                  Home
                </NavLink>
              </li>
              {roseLinks}
            </ul>
          </div>
        </div>
      </nav>
    );
  }
}
export default Navbar;
