import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./RoseDetail.css";
class RoseDetail extends React.Component {
  render() {
    let { rose } = this.props;
    return (
      <div className="RoseDetail row justify-content-center mt-4">
        <div className="col-10 col-lg-4 ">
          <div className="RoseDetail-card card my-4 ">
            <img
              className="card-img-top mx-auto"
              src={rose.src}
              alt={rose.name}
            />

            <div className="card-body text-capitalize">
              <h2 className="card-title">{rose.name}</h2>
            </div>
            <ul className="list-group list-group-flush">
              {rose.facts.map((fact, i) => (
                <li className="list-group-item text-capitalize" key={i}>
                  {fact}
                </li>
              ))}
            </ul>
            <div className="card-body text-capitalize">
              <Link to="/roses" className="btn btn-info">
                go back
              </Link>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default RoseDetail;
