import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./RoseList.css";
class RoseList extends React.Component {
  render() {
    return (
      <div className="RoseList">
        <h2 className="display-6 text-center text-capitalize fw-bold my-4">
          rose list
        </h2>

        <div className="row">
          {this.props.roses.map(r => (
            <div className="Rose col-lg-3 text-center" key="r.name ">
              <img
                className="shadow p-3 mb-4 rounded"
                src={r.src}
                alt={r.name}
              />
              <h4>
                <Link
                  to={`/roses/${r.name}`}
                  className="underline text-decoration-none text-capitalize"
                >
                  {r.name}
                </Link>
              </h4>
            </div>
          ))}
        </div>
      </div>
    );
  }
}

export default RoseList;
